























































import { Action, State, Getter } from 'vuex-class';
import { IArticle, IExamInfoListInput, IListItem } from '@/types/interface';
import { Component, Vue } from 'vue-property-decorator';
import Banner from '../../components/Banner.vue';
import ExamInfoListItem from '../../components/ExamInfoListItem.vue';
import inputCustom from '../../components/InputCustom.vue';
import pagination from '../../components/Pagination.vue';

@Component({
  components: {
    Banner,
    ExamInfoListItem,
    inputCustom,
    pagination,
  },
})

export default class ExamInfoPage extends Vue {
  @Action('getExamInfoList', { namespace: 'examInfo' }) getExamInfoList!: (params: IExamInfoListInput) => void;
  @Action('getExamInfoSimple', { namespace: 'examInfo' }) getExamInfoSimple!: (id: number) => Promise<void>;
  @Action('getTopic', { namespace: 'subscription' }) getTopic!: () => void;
  @Action('getUserSub', { namespace: 'subscription' }) getUserSub!: () => void;
  @State('examInfoList', { namespace: 'examInfo' }) stateExamInfoList!: IArticle[];
  @Getter('userSubTopicList', { namespace: 'subscription' }) userSubTopicList!: IListItem[] | [];
  @Getter('filterTopicList', { namespace: 'subscription' }) filterTopicList!: IListItem[] | [];
  @Getter('allTopicList', { namespace: 'subscription' }) allTopicList!: IListItem[] | [];

  isExpandSub = false;
  isExpandAll = false;
  isSearch = false;
  searchModel = '';
  pageSplitNum = 4;
  pageIdx = 1;
  searchNum = 0;

  get filterList() {
    return this.isExpandSub ? this.userSubTopicList : this.allTopicList;
  }

  get filterExamInfoList() {
    if (this.tagSelected === '') return this.stateExamInfoList;
    return this.stateExamInfoList.filter((d: IArticle) => (d.hashtags ? d.hashtags.indexOf(this.tagSelected) >= 0 : false));
  }

  get sliceList() {
    return this.filterExamInfoList.slice((this.pageIdx - 1) * this.pageSplitNum, this.pageIdx * this.pageSplitNum);
  }

  get pages() {
    return Math.ceil(this.filterExamInfoList.length / this.pageSplitNum);
  }

  get tagSelected() {
    return this.$route.query.tag ? JSON.parse(JSON.stringify(this.$route.query.tag)) : '';
  }

  get searchText() {
    return this.$route.query.searchText ? JSON.parse(JSON.stringify(this.$route.query.searchText)) : '';
  }

  get keyword() {
    return this.$route.query.keyword ? JSON.parse(JSON.stringify(this.$route.query.keyword)) : '';
  }

  get keywordId() {
    return this.$route.query.keywordId ? JSON.parse(JSON.stringify(this.$route.query.keywordId)) : '';
  }

  get selectSub() {
    return this.userSubTopicList.findIndex((d: IListItem) => d.id === this.keywordId) >= 0;
  }

  get selectAll() {
    return this.allTopicList.findIndex((d: IListItem) => d.id === this.keywordId) >= 0;
  }

  get selectType() {
    return this.$route.query.type ? JSON.parse(JSON.stringify(this.$route.query.type)) : '';
  }

  get compareSelectAndExpand() {
    return (this.selectType === 'all' && this.isExpandAll) || (this.selectType === 'sub' && this.isExpandSub);
  }

  created() {
    this.searchModel = this.searchText;
    this.isSearch = this.searchText !== '';
    if (this.tagSelected === '') {
      this.getTopic();
      this.getUserSub();
    }
    this.getListData();
  }

  link() {
    if (this.tagSelected === '') this.$router.push('/main');
    else this.$router.go(-1);
  }

  show(id: number) {
    this.getExamInfoSimple(id)
      .then(() => {
        this.$router.push({ path: '/main/exam_info_content', query: { id: id.toString() } });
      });
  }

  search() {
    this.searchNum = 0;
    this.isSearch = true;
  }

  cancelHandler() {
    this.searchModel = '';
    this.isSearch = false;
    this.$router.go(this.searchNum);

    this.getListData();
  }

  expandSub() {
    if (this.isExpandAll) this.isExpandAll = false;
    this.isExpandSub = !this.isExpandSub;
  }

  expandAll() {
    if (this.isExpandSub) this.isExpandSub = false;
    this.isExpandAll = !this.isExpandAll;
  }

  changeIdx(i: number) {
    this.pageIdx = i;
  }

  searchHandler() {
    this.searchNum -= 1;
    this.$router.push({
      path: '/main/exam_info',
      query: {
        searchText: this.searchModel,
        tag: this.tagSelected === '' ? undefined : this.tagSelected,
        keyword: this.keyword === '' ? undefined : this.keyword,
        keywordId: this.keywordId === '' ? undefined : this.keywordId,
      },
    });

    this.getListData();
  }

  selectHandler(item: IListItem) {
    this.$router.push({
      path: '/main/exam_info',
      query: {
        searchText: this.searchText === '' ? undefined : this.searchText,
        tag: this.tagSelected === '' ? undefined : this.tagSelected,
        keyword: item.id === this.keywordId && this.compareSelectAndExpand ? undefined : item.title,
        keywordId: item.id === this.keywordId && this.compareSelectAndExpand ? undefined : item.id,
        type: this.isExpandAll ? 'all' : 'sub',
      },
    });

    this.getListData();

    this.isExpandAll = false;
    this.isExpandSub = false;
  }

  getListData() {
    const temp = this.tagSelected !== '' ? this.tagSelected : this.keyword;

    this.getExamInfoList({
      isLongPeriod: true,
      isForAll: this.selectType === 'all',
      searchKey: this.searchText !== '' ? this.searchText : '',
      keyword: this.searchText === '' && temp !== '' ? temp : '',
    });
  }
}
