


























import {
  Component, Vue, Prop, Emit, Watch,
} from 'vue-property-decorator';

@Component
export default class Pagination extends Vue {
  @Prop() pages!: number;

  selectedTemp = 1;
  startIdx = 1;
  endIdx = 1;

  @Emit()
  selectExec(idx: number) {
    return idx;
  }

  @Watch('pages', { immediate: true })
  pagesChange(nVal: number) {
    this.selectedTemp = 1;
    this.startIdx = 1;
    this.endIdx = (nVal > 4) ? 4 : nVal;
  }

  get selected() {
    return this.selectedTemp;
  }

  set selected(idx) {
    this.selectExec(idx);
    this.selectedTemp = idx;
  }

  get showPagesList() {
    const temp = [];
    for (let i = 0; i < this.pages; i += 1) {
      if ((i + 1) >= this.startIdx && (i + 1) <= this.endIdx) {
        temp.push(i + 1);
      }
    }
    return temp;
  }

  switchPage(page: number) {
    this.selected = page;
  }

  switchToStart() {
    this.selected = 1;
    this.startIdx = 1;
    this.endIdx = (this.pages > 4) ? 4 : this.pages;
  }

  switchToEnd() {
    this.selected = this.pages;
    this.startIdx = (this.pages - 3 < 1) ? 1 : this.pages - 3;
    this.endIdx = this.pages;
  }

  switchToPrev() {
    if (this.selected === 1) return;
    this.selected -= 1;
    if (this.startIdx > this.selected) {
      this.startIdx -= 1;
      this.endIdx = (this.startIdx === 1) ? this.startIdx + 3 : this.startIdx + 2;
    }
  }

  switchToNext() {
    if (this.selected === this.pages) return;
    this.selected += 1;
    if (this.endIdx < this.selected) {
      this.endIdx += 1;
      this.startIdx = (this.endIdx === this.pages) ? this.endIdx - 3 : this.endIdx - 2;
    }
  }

  switchToNextPhase() {
    const temp = this.selected + 4;
    if (temp >= this.pages) {
      this.switchToEnd();
    } else {
      this.selected = temp;
      this.endIdx = (this.selected + 3 >= this.pages) ? this.pages : this.selected + 2;
      this.startIdx = (this.endIdx === this.pages) ? this.endIdx - 3 : this.endIdx - 2;
    }
  }

  switchToPrevPhase() {
    const temp = this.selected - 4;
    if (temp <= 1) {
      this.switchToStart();
    } else {
      this.selected = temp;
      this.startIdx = (this.selected - 3 <= 1) ? 1 : this.selected - 2;
      this.endIdx = (this.startIdx === 1) ? this.startIdx + 3 : this.startIdx + 2;
    }
  }
}
